/* 1. Use a more-intuitive box-sizing model. */
*, *::before, *::after {
    box-sizing: border-box;
}

/* 2. Remove default margin */
* {
    margin: 0;
    padding: 0
}

/* Typographic tweaks!
3. Add accessible line-height
4. Improve text rendering */
body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;

    /* immediate children of body */
    > * {
        padding: var(--padding);
    }
}

/* 5. Improve media defaults */
img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
}

/* 6. Remove built-in form typography styles */
input, button, textarea, select {
    font: inherit;
    appearance: none;
}

/* 7. Avoid text overflows */
p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
}

/* 8. Create a root stacking context */
#root, #__next {
    isolation: isolate;
} 

/* 9. Reset link styling */
a {
    appearance: none;
    color: inherit;
    text-decoration: none;
}

/* 10. lists */
ul {
    padding-left: var(--padding);
}`, "",{"version":3,"sources":["webpack://./src/reset.css"],"names":[],"mappings":"AAAA,kDAAkD;;;AAGlD,8CAA8C;AAC9C;IACI,sBAAsB;AAC1B;;AAEA,6BAA6B;AAC7B;IACI,SAAS;IACT;AACJ;;AAEA;;2BAE2B;AAC3B;IACI,gBAAgB;IAChB,mCAAmC;;IAEnC,+BAA+B;IAC/B;QACI,uBAAuB;IAC3B;AACJ;;AAEA,8BAA8B;AAC9B;IACI,cAAc;IACd,eAAe;AACnB;;AAEA,8CAA8C;AAC9C;IACI,aAAa;IACb,gBAAgB;AACpB;;AAEA,4BAA4B;AAC5B;IACI,yBAAyB;AAC7B;;AAEA,sCAAsC;AACtC;IACI,kBAAkB;AACtB;;AAEA,0BAA0B;AAC1B;IACI,gBAAgB;IAChB,cAAc;IACd,qBAAqB;AACzB;;AAEA,cAAc;AACd;IACI,4BAA4B;AAChC","sourcesContent":["/* ==================reset.css================== */\n\n\n/* 1. Use a more-intuitive box-sizing model. */\n*, *::before, *::after {\n    box-sizing: border-box;\n}\n\n/* 2. Remove default margin */\n* {\n    margin: 0;\n    padding: 0\n}\n\n/* Typographic tweaks!\n3. Add accessible line-height\n4. Improve text rendering */\nbody {\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n\n    /* immediate children of body */\n    > * {\n        padding: var(--padding);\n    }\n}\n\n/* 5. Improve media defaults */\nimg, picture, video, canvas, svg {\n    display: block;\n    max-width: 100%;\n}\n\n/* 6. Remove built-in form typography styles */\ninput, button, textarea, select {\n    font: inherit;\n    appearance: none;\n}\n\n/* 7. Avoid text overflows */\np, h1, h2, h3, h4, h5, h6 {\n    overflow-wrap: break-word;\n}\n\n/* 8. Create a root stacking context */\n#root, #__next {\n    isolation: isolate;\n} \n\n/* 9. Reset link styling */\na {\n    appearance: none;\n    color: inherit;\n    text-decoration: none;\n}\n\n/* 10. lists */\nul {\n    padding-left: var(--padding);\n}"],"sourceRoot":""}]);
